import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index.vue'


Vue.use(Router)

const indexRouters = [
    {
        path: '',
        name: 'ArticleListContainer',
        component:  resolve => require(['./components/container/ArticleListContainer.vue'],resolve)
    },
    {
        path: '/readArticle',
        name: 'ReadContainer',
        component:  resolve => require(['./components/container/ReadContainer.vue'],resolve)
    }
];

export default new Router({
    mode: 'history',
    scrollBehavior: () => ({ y: 0 }),
    routes: [
        {
            path: '/',
            name: 'index',
            component: Index,
            redirect:{name:'ArticleListContainer'},
            children: indexRouters
        }
        // {
        //     path: '/about',
        //     name: 'about',
        //     // route level code-splitting
        //     // this generates a separate chunk (about.[hash].js) for this route
        //     // which is lazy-loaded when the route is visited.
        //     component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
        // }
    ]
})


